import { Injectable } from '@angular/core';
import { SQLite, SQLiteObject } from '@ionic-native/sqlite';
import { Buffer } from 'buffer';

@Injectable()
export class SQLiteProvider {

  database: SQLiteObject;

  constructor(private sqlite: SQLite) {
    this.creatDB();
  }

  creatDB() {
    this.sqlite.create({
      name: 'data.db',
      location: 'default'
    }).then((db: SQLiteObject) => {
  
      db.executeSql('CREATE TABLE IF NOT EXISTS USER(USERID VARCHAR(8),PASSWORD VARCHAR(50))', {})
        .then(() => console.log('creatDB'))
        .catch(e => console.log(e));
      this.database = db;
    }).catch(e => console.log(e));
  }

  insertData(userId, password) {

    password = this.encryptio(password);
    password = new Buffer(password).toString('base64');
    console.log("this is encryptio",password);

    if (this.database == undefined || this.database == null) {
      return null;
    }
    this.database.executeSql("INSERT INTO USER(USERID, PASSWORD) VALUES (?,?)", [userId, password])
      .then(() => console.log('insertData'))
      .catch(e => console.log(e));
  }

  deleteData() {
    if (this.database == undefined || this.database == null) {
      return null;
    }
    this.database.executeSql("DELETE FROM USER", {})
      .then(() => console.log('deleteData'))
      .catch(e => console.log(e));
  }

  async selectData() {
    let rlt;

    if (this.database == undefined || this.database == null) {
      return rlt;
    }

    await this.database.executeSql("SELECT USERID AS userid, PASSWORD AS passwd FROM USER;", {})
      .then((data) => {
        if (data.rows.length > 0) {
          let password = this.decryption(new Buffer(data.rows.item(0).passwd,'base64').toString());
          console.log("this is  nn",password);
          rlt = { "userID": data.rows.item(0).userid, "passWD": password};
        
        }
      })
      .catch(e => console.log(e));
    return rlt;
  }
  _keyStr = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=";
  encryptio(password){
    let output = "";  
    let chr1, chr2, chr3, enc1, enc2, enc3, enc4;  
    let i = 0;  
    let input = this._utf8_encryption(password);  
    while (i < input.length) {  
        chr1 = input.charCodeAt(i++);  
        chr2 = input.charCodeAt(i++);  
        chr3 = input.charCodeAt(i++);  
        enc1 = chr1 >> 2;  
        enc2 = ((chr1 & 3) << 4) | (chr2 >> 4);  
        enc3 = ((chr2 & 15) << 2) | (chr3 >> 6);  
        enc4 = chr3 & 63;  
        if (isNaN(chr2)) {  
            enc3 = enc4 = 64;  
        } else if (isNaN(chr3)) {  
            enc4 = 64;  
        }  
        output = output +  
        this._keyStr.charAt(enc1) + this._keyStr.charAt(enc2) +  
        this._keyStr.charAt(enc3) + this._keyStr.charAt(enc4);  
    }  
    return output;
  }


  _utf8_encryption(password){
    let utftext = "";
    password = password.replace(/\r\n/g,"\n");
    for(let i = 0;i < password.length;i++){
      let c = password.charCodeAt(i);
      if (c < 128) {  
        utftext += String.fromCharCode(c);  
      } else if((c > 127) && (c < 2048)) {  
        utftext += String.fromCharCode((c >> 6) | 192);  
        utftext += String.fromCharCode((c & 63) | 128);  
      } else {  
        utftext += String.fromCharCode((c >> 12) | 224);  
        utftext += String.fromCharCode(((c >> 6) & 63) | 128);  
        utftext += String.fromCharCode((c & 63) | 128);  
      } 
    }
    return utftext;
  }
  decryption(input){
    let output = "";  
    let chr1, chr2, chr3;  
    let enc1, enc2, enc3, enc4;  
    let i = 0;  
    input = input.replace(/[^A-Za-z0-9\+\/\=]/g, "");  
    while (i < input.length) {  
        enc1 = this._keyStr.indexOf(input.charAt(i++));  
        enc2 = this._keyStr.indexOf(input.charAt(i++));  
        enc3 = this._keyStr.indexOf(input.charAt(i++));  
        enc4 = this._keyStr.indexOf(input.charAt(i++));  
        chr1 = (enc1 << 2) | (enc2 >> 4);  
        chr2 = ((enc2 & 15) << 4) | (enc3 >> 2);  
        chr3 = ((enc3 & 3) << 6) | enc4;  
        output = output + String.fromCharCode(chr1);  
        if (enc3 != 64) {  
            output = output + String.fromCharCode(chr2);  
        }  
        if (enc4 != 64) {  
            output = output + String.fromCharCode(chr3);  
        }  
    }  
    output = this._utf8_decryption(output);  
    return output;
  }
  _utf8_decryption(utftext){
    let string = "";  
    let i = 0; 
    let c =0, c2 = 0, c3 = 0;  
    while ( i < utftext.length ) {  
        c = utftext.charCodeAt(i);  
        if (c < 128) {  
            string += String.fromCharCode(c);  
            i++;  
        } else if((c > 191) && (c < 224)) {  
            c2 = utftext.charCodeAt(i+1);  
            string += String.fromCharCode(((c & 31) << 6) | (c2 & 63));  
            i += 2;  
        } else {  
            c2 = utftext.charCodeAt(i+1);  
            c3 = utftext.charCodeAt(i+2);  
            string += String.fromCharCode(((c & 15) << 12) | ((c2 & 63) << 6) | (c3 & 63));  
            i += 3;  
        }  
    }  
    return string; 
  }

}


