import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { AppMinimize } from '@ionic-native/app-minimize';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { ScreenOrientation } from '@ionic-native/screen-orientation';

import { MyApp } from './app.component';
import { LoginPage } from '../pages/login/login';
import { ControlPage } from '../pages/control/control';
import { SchedulePage } from '../pages/schedule/schedule';
import { TaskrdPage } from '../pages/taskrd/taskrd';
import { SelstorePage } from '../pages/selstore/selstore';
import { TaskuPage } from '../pages/tasku/tasku';
import { ManualPage } from '../pages/manual/manual';
import { HandovercPage } from '../pages/handoverc/handoverc';
import { HandoveruPage } from '../pages/handoveru/handoveru';
import { HandoverrdPage } from '../pages/handoverrd/handoverrd';
import { TaskcPage } from '../pages/taskc/taskc';
import { HandoverpopPage } from '../pages/handoverpop/handoverpop';
import { DragulaModule } from 'ng2-dragula';
import { DatePicker } from '@ionic-native/date-picker';
import { HttpClientModule } from '@angular/common/http';
import { Camera } from '@ionic-native/camera';
import { BoardPage } from '../pages/board/board';
import { Keyboard } from '@ionic-native/keyboard';
import { SQLite } from '@ionic-native/sqlite';

import { ApiScheduleServiceProvider } from '../providers/api-service/api-schedule-service';
import { ApiControlServiceProvider } from '../providers/api-service/api-control-service';
import { ApiHandoverServiceProvider } from '../providers/api-service/api-handover-service';
import { ApiLoginServiceProvider } from '../providers/api-service/api-login-service';
import { ApiManualServiceProvider } from '../providers/api-service/api-manual-service';
import { ApiSelstoreServiceProvider } from '../providers/api-service/api-selstore-service';
import { ApiTaskrdServiceProvider } from '../providers/api-service/api-taskrd-service';
import { ApiTaskuServiceProvider } from '../providers/api-service/api-tasku-service';
import { ApiTaskcServiceProvider } from '../providers/api-service/api-taskc-service';
import { BackExitServiceProvider } from '../providers/back-exit/back-Exit';
import { ApiCommonProvider } from '../providers/api-common/api-common';
import { HttpModule } from '@angular/http';
import { IonicStorageModule } from '@ionic/storage';
import { SelschedulePage } from '../pages/selschedule/selschedule';
import { SQLiteProvider } from '../providers/sqlite/sqlite';
import { S3Util } from '../providers/s3-common/s3.service';
import { Push } from '@ionic-native/push';
import { Device } from '@ionic-native/device';
import { StoreTimer } from '@ionic-native/store-timer';

@NgModule({
  declarations: [
    MyApp,
    SchedulePage,
    ControlPage,
    LoginPage,
    TaskrdPage,
    SelstorePage,
    TaskuPage,
    ManualPage,
    HandovercPage,
    HandoveruPage,
    HandoverrdPage,
    TaskcPage,
    HandoverpopPage,
    SelschedulePage,
    BoardPage
  ],
  imports: [
    BrowserModule,
    DragulaModule,
    IonicModule.forRoot(MyApp),
    HttpClientModule,
    HttpModule,
    IonicStorageModule.forRoot({
      name: '__storedb',
      driverOrder: ['indexeddb', 'websql', 'sqlite']
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    SchedulePage,
    ControlPage,
    LoginPage,
    TaskrdPage,
    SelstorePage,
    TaskuPage,
    ManualPage,
    HandovercPage,
    HandoveruPage,
    HandoverrdPage,
    TaskcPage,
    HandoverpopPage,
    SelschedulePage,
    BoardPage
  ],
  providers: [
    AppMinimize,
    Device,
    StoreTimer,
    Push,
    Camera,
    Keyboard,
    DatePicker,
    StatusBar,
    ScreenOrientation,
    SplashScreen,
    SQLite,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    ApiCommonProvider,
    ApiControlServiceProvider,
    ApiHandoverServiceProvider,
    ApiLoginServiceProvider,
    ApiManualServiceProvider,
    ApiSelstoreServiceProvider,
    ApiTaskrdServiceProvider,
    ApiTaskuServiceProvider,
    ApiTaskcServiceProvider,
    ApiScheduleServiceProvider,
    SQLiteProvider,
    S3Util,
    BackExitServiceProvider
  ]
})
export class AppModule { }



