import { Http, Headers } from '@angular/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';

/*
  Generated class for the ApiCommonProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

/**
 * 
 * 概 要： AWSの通信処理
 * 作成日：2018.01.30
 * 作成者：DHC孫志棟
 */
@Injectable()
export class ApiCommonProvider {
    accessToken;
    //  host = 'https://zm7tf6uimg.execute-api.ap-northeast-1.amazonaws.com/dev';
    //  host = 'https://bmd91rtw4a.execute-api.ap-northeast-1.amazonaws.com/dev';
    host = 'https://seoej0sn2b.execute-api.ap-northeast-1.amazonaws.com/dev';
    constructor(public http: Http,
                private storage: Storage) {
                }
                
    async doPost(path: string, param: Object) {
        this.accessToken = await this.storage.get('accessToken');
        let addHeaders = new Headers({'Authorization': this.accessToken});
        this.storage.set('autoRefreshTimeCounter', 0);
        
        try {
            // 自動更新が初期化にする
            let res = await this.http.post(this.host + path, 
                      JSON.stringify(param), {headers: addHeaders}).toPromise();
            
            let result = res.json();
            let accessToken = result['accessToken'];
            this.storage.set('accessToken', accessToken);

            return res;
        } catch(error) {
            console.log("http post error:" + error);
        }

    }

    async doGet(path: string, param?: Object) {
        this.accessToken = await this.storage.get('accessToken');
        let addHeaders = new Headers({'Authorization': this.accessToken});
        this.storage.set('autoRefreshTimeCounter', 0);
        try {
            // 自動更新が初期化にする
            let res = await this.http.get(this.host + path, {headers: addHeaders}).toPromise();
    
            let result = res.json();
            let accessToken = result['accessToken'];
            this.storage.set('accessToken', accessToken);
            return res;
        } catch(error) {
            console.log("http get error:" + error);
        }
    }

    async doDelete(path: string, param?: Object) {
        this.accessToken = await this.storage.get('accessToken');
        let addHeaders = new Headers({'Authorization': this.accessToken});
        this.storage.set('autoRefreshTimeCounter', 0);

        try {
            // 自動更新が初期化にする
            let res = await this.http.delete(this.host + path, {headers: addHeaders}).toPromise();
    
            let result = res.json();
            let accessToken = result['accessToken'];
            this.storage.set('accessToken', accessToken);
            return res;
        } catch(error) {
            console.log("http delete error:" + error);
        }
    }

    async doPut(path: string, param?: Object) {
        this.accessToken = await this.storage.get('accessToken');
        let addHeaders = new Headers({'Authorization': this.accessToken});
        this.storage.set('autoRefreshTimeCounter', 0);

        try {
            // 自動更新が初期化にする
            let res = await this.http.put(this.host + path, JSON.stringify(param), {headers: addHeaders}).toPromise();
    
            let result = res.json();
            let accessToken = result['accessToken'];
            this.storage.set('accessToken', accessToken);
            return res;
        } catch(error) {
            console.log("http put error:" + error);
        }
    }

}



