import { Component } from '@angular/core';
import { AlertController } from 'ionic-angular'
import { SelstorePage } from '../selstore/selstore';
import { NavController } from 'ionic-angular';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ApiLoginServiceProvider } from '../../providers/api-service/api-login-service'
import { LoadingController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { ViewCommon } from '../../providers/view-common/ViewCommon';
import { SQLiteProvider } from '../../providers/sqlite/sqlite';
import { Device } from '@ionic-native/device';
import { Keyboard } from '@ionic-native/keyboard';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-login',
    templateUrl: 'login.html',
})

/**
 * 
 * 概 要： ログイン処理
 * 作成日：2018.01.30
 * 作成者：DHC孫志棟
 */
export class LoginPage {
    private password: any;
    private aplUserId: any;
    private loginForm: FormGroup;
    public static loginLoading;
    private imgHid = false;
    private userId;
    private passwd;

    constructor(public navCtrl: NavController,
        public alertCtrl: AlertController,
        public service: ApiLoginServiceProvider,
        private formBuilder: FormBuilder,
        private loadingCtrl: LoadingController,
        private sqlite: SQLiteProvider,
        private storage: Storage,
        public keyboard: Keyboard,
        private device: Device) {

        this.loginForm = this.formBuilder.group({
            aplUserId: ['', Validators.compose([Validators.maxLength(8), Validators.required])],
            password: ['', Validators.compose([Validators.required])]
        })

        this.password = this.loginForm.controls['password'];
        this.aplUserId = this.loginForm.controls['aplUserId'];
    }

    ionViewWillEnter() {
        this.getUser();
        this.keyboard.onKeyboardShow().subscribe(() => {
            this.imgHid = true;
            document.body.classList.add('keyboard-is-open');
            let sContent = document.getElementsByClassName('scroll-content');
            if (sContent.length > 0) {
                if (sContent[0] != undefined && sContent[0] != null) {
                    sContent[0].setAttribute('style', 'padding-bottom: 0px;');
                }
            }
        });
        this.keyboard.onKeyboardHide().subscribe(() => {
            this.imgHid = false;
            document.body.classList.remove('keyboard-is-open');
        });
    }

    async getUser() {
        let result = await this.sqlite.selectData();

        if (result != undefined && result != null) {
            this.userId = result.userID;
            this.passwd = result.passWD;
        }
    }

    /**
     * 入力のユーザーID、パスワードでログインする
     * @param value フォームの項目バリュー
     */
    async login(value) {
        // isShowの初期化
        this.storage.remove('isShow');
        LoginPage.loginLoading = this.loadingCtrl.create({
            content: ''
        });

        LoginPage.loginLoading.present();
        let uuid;
        if (this.device.uuid == null) {
            uuid = 'hasnotuuid';
        } else {
            uuid = this.device.uuid;
        }
        let res = await this.service.login(value.aplUserId, uuid, value.password);

        if (res == undefined) {
            LoginPage.loginLoading.dismiss();
            ViewCommon.showAlert(this.alertCtrl, '通信エラーが発生しました');
            return;
        }

        let result = res.json();
        let msgInfo = result['msgInfo'];
        if (msgInfo.statusCd == 'SB200') {
            this.storage.set('accessToken', result.accessToken);

            this.sqlite.deleteData();
            this.sqlite.insertData(value.aplUserId, value.password);

            this.navCtrl.setRoot(SelstorePage, { aplUserId: value.aplUserId, deviceId: uuid });
        } else {
            LoginPage.loginLoading.dismiss();
            ViewCommon.showAlert(this.alertCtrl, msgInfo.msg);
        }
    }

    hidImg() {
        this.imgHid = true;
    }
}



