import { Component } from '@angular/core';
import { App, IonicPage, NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { SchedulePage } from '../schedule/schedule';
import { LoginPage } from '../login/login';
import { ApiScheduleServiceProvider } from '../../providers/api-service/api-schedule-service';
import { ApiSelstoreServiceProvider } from '../../providers/api-service/api-selstore-service';
import { UtilityService } from '../../providers/utility-service/UtilityService';
import { Push, PushObject, PushOptions } from '@ionic-native/push';
import { ViewCommon } from '../../providers/view-common/ViewCommon';

/**
 * Generated class for the SelstorePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-selstore',
    templateUrl: 'selstore.html',
})
export class SelstorePage {

    private userId;
    private shopList = [];
    private storeNoChecked;
    private storeNmChecked;
    private registrationId: any;
    constructor(public navCtrl: NavController,
        public navParams: NavParams,
        public alertCtrl: AlertController,
        public loadingCtrl: LoadingController,
        private appCtrl: App,
        public service: ApiSelstoreServiceProvider,
        public push: Push,
        private scheduleProvider: ApiScheduleServiceProvider) {
        this.userId = this.navParams.get('aplUserId');
    }

    ionViewDidLoad() {
        this.selstore();
    }

    shopChecked(no, nm) {
        this.storeNoChecked = no;
        this.storeNmChecked = nm;
    }

    goBack() {
        this.navCtrl.setRoot(LoginPage);
    }

    async goHome() {
        let loading = this.loadingCtrl.create({
            content: ''
        });

        loading.present();
        let date = new Date();
        let selDate = UtilityService.formatDate(date);
        let selTime = UtilityService.formatTime(date.getHours(), date.getMinutes());

        let res = await this.scheduleProvider.getScheduleInit(
            this.storeNoChecked, selDate, selTime, '0');
        let result = res.json();
        let msgInfo = result['msgInfo'];
        if (msgInfo.msgCd == 'MSE400003') {
            ViewCommon.showAlert(this.alertCtrl, msgInfo.msg);
            loading.dismiss();
            return;
        }
        loading.dismiss();
        // 自動通知　要件がなくなるので、一旦コメントアウト
        this.navCtrl.setRoot(SchedulePage,
            {
                storeNo: this.storeNoChecked, storeNm: this.storeNmChecked,
                res: res, aplUserId: this.userId, viewKbn: '0', selectedText: 'カレンダー( 業務別 )',
                selectedDate: selDate
            });
    }

    async selstore() {

        let res = await this.service.selstore(this.userId);
        LoginPage.loginLoading.dismiss();

        if (res == undefined) {
            ViewCommon.showAlert(this.alertCtrl, '通信エラーが発生しました');
            return;
        } else {
            let result = res.json();
            let msgInfo = result['msgInfo'];
            if (msgInfo.statusCd == 'SB200') {
                this.shopList = result['storeList']
                if (this.shopList.length > 0) {
                    this.storeNoChecked = this.shopList[0].storeNo;
                    this.storeNmChecked = this.shopList[0].storeNm;
                }
            } else if (msgInfo.statusCd == 'SB900') {
                let alert = this.alertCtrl.create({
                    title: 'エラー',
                    subTitle: msgInfo.msg,
                    buttons: [
                        {
                            text: 'OK',
                            handler: data => {
                                this.appCtrl.getRootNav().setRoot(LoginPage);
                            }
                        },
                    ]
                });
                alert.present();
                return;
            } else {
                ViewCommon.showAlert(this.alertCtrl, msgInfo.msg);
            }
        }
    }
    initPushNotification() {
        // to check if we have permission
        this.push.hasPermission()
            .then((res: any) => {
                if (res.isEnabled) {
                    console.log('We have permission to send push notifications');
                } else {
                    console.log('We do not have permission to send push notifications');
                }
            }).catch((err) => {
                console.error('Error', err);
                return;
            });

        // to initialize push notifications
        const options: PushOptions = {
            android: {
                senderID: '759576800446'
            },
            ios: {
                alert: 'true',
                badge: true,
                sound: 'false'
            },
            windows: {},
            browser: {}
        };

        const pushObject: PushObject = this.push.init(options);

        pushObject.on('notification').subscribe((notification: any) => {
            let xmsg = notification.additionalData.default;
            let jmsg = JSON.parse(xmsg.GCM);
            let msg = jmsg.data.Message;
            let alert = this.alertCtrl.create({
                title: '警告',
                subTitle: msg,
                buttons: ['OK']
            });
            alert.present();
        }
        );

        pushObject.on('registration').subscribe((registration: any) => {
            console.log('on registration');
            this.registrationId = registration.registrationId;
            this.insertPush(this.registrationId);
        });
    }

    // registrationIdのデータを挿入する
    async insertPush(deviceId) {
        let res = await this.service.registDevice(this.userId, deviceId, this.storeNoChecked);

        if (res == undefined) {
            ViewCommon.showAlert(this.alertCtrl, '通信エラーが発生しました');
            return;
        }
        let result = res.json();
        let msgInfo = result['msgInfo'];
        if (msgInfo.statusCd != 'SB200') {
            ViewCommon.showAlert(this.alertCtrl, msgInfo.msg);
        }
    }

}



