import { Injectable } from '@angular/core';
import { ApiCommonProvider } from '../api-common/api-common';

/*
  Generated class for the LoginProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

/**
 * 
 * 概 要： ログインのロジック処理
 * 作成日：2018.01.30
 * 作成者：DHC孫志棟
 */
@Injectable()
export class ApiLoginServiceProvider {

    constructor(public commonService: ApiCommonProvider) {
    }

    /**
     * ユーザーID、パスワードで、AWSへ通信する
     * 
     * @param userId ユーザーID
     * @param password パスワード
     */
    async login(userId, deviceId, password) {
        let param = { 'aplUser': userId, 'deviceId': deviceId, 'passwd': password };
        let res = await this.commonService.doPost('/auth', param);

        return res;
    }
}



